<template>
<v-app>
   <v-data-table
    :search="search"
    :headers="headers"
    :items="desserts"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>Les demandes </v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
         <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          >
      </v-text-field>
        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  Voulez vous accepter ou refuser?
                </v-row>
                <v-row>
                  <v-radio-group v-model="editedItem.status">
                    <v-radio
                      value="accepter"
                      label="Accepter"
                      color="green"
                    >
                    </v-radio>
                    <v-radio
                      value="refuser"
                      label="Refuser"
                      color="red"
                    >
                    </v-radio>
                  </v-radio-group>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</v-app>
</template>
<script>
//import Nav from './responsable_home.vue'
import Axios from 'axios'
  export default {
    components: {
    //  Nav
    },
    data: () => ({
      search: " ",
      direction: '',
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Sujet',
          align: 'start',
          sortable: false,
          value: 'sujet',
        },
        { text: 'Status', value: 'status' },
        { text: 'Description', value: 'descri' },
        { text: 'Date demande ', value: 'date' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        id:'',
        id_etd:'',

        sujet: '',
        status:'',

        descri: '',
        date: '',
      },
      demandes:[],
      defaultItem: {
           id:'',
        id_etd:'',
        sujet: '',
        status:'',
        descri: '',
        date: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        if(localStorage.nom){
            Axios.get(
                "http://localhost:3000/demande"
              ).then((res) => {
                res.data.forEach((item) => {
                if ( item.status != 'pas encore' )  {
                     this.desserts.push({
                          id:item.id,
                          id_etd:localStorage.id,
                         sujet: item.sujet,
                         status:item.status,
                         descri: item.description,
                         date: item.date_demande
          });
                  }
                })
              });
              }
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
            Axios.put("http://localhost:3000/demande/"+this.editedItem.id,{
            id:localStorage.id,
            id_etd: localStorage.id,
            sujet:this.editedItem.sujet,
            date_demande: new Date().toJSON().slice(0,10),
            description: this.editedItem.descri,
            status: this.editedItem.status,
            id_user: localStorage.id}).then(() => {
            })
            Object.assign(this.desserts[this.editedIndex], this.editedItem)
            this.close();
      },
    },
  }
</script>